import * as localizedMessages from "../__localization__";
import { defineMessages, useIntl } from "react-intl";
import { DataItem } from "../types";
import { IntlProvider } from "@quarry/intl";
import React from "react";
import { LineChart as VCCLineChart } from "@visa/charts-react";
import processData from "../processData";

const messages = defineMessages({
  month: {
    id: "line-chart.month",
    defaultMessage: "Month",
  },
  impressions: {
    id: "line-chart.impressions",
    defaultMessage: "Impressions",
  },
  toggle: {
    id: "line-chart.toggle-aria-label",
    defaultMessage: "Toggle menu",
  },
});

interface VCCLineChartProps {
  /** Locale used to internationalize the component. */
  locale?: string;
  colors?: string[];
  toolTipAccessors: string[];
  toolTipTitle: string[];
  width?: number;
  height?: number;
  viewTakeaways?: boolean;
}

const data: DataItem[] = [
  {
    key: "0",
    month: "Apr 17",
    value: 1407,
    series: "Mock Data",
  },
  {
    key: "2",
    month: "Jun 17",
    value: 2207,
    series: "Mock Data",
  },
  {
    key: "0",
    month: "Apr 17",
    value: 140,
    series: "Mock Data2",
  },
  {
    key: "1",
    month: "May 17",
    value: 1550,
    series: "Mock Data2",
  },
  {
    key: "1",
    month: "May 17",
    value: 1807,
    series: "Mock Data",
  },
  {
    key: "3",
    month: "Jul 17",
    value: 1000,
    series: "Mock Data3",
  },
  {
    key: "2",
    month: "Jun 17",
    value: 590,
    series: "Mock Data2",
  },
  {
    key: "0",
    month: "Apr 17",
    value: 600,
    series: "Mock Data3",
  },
  {
    key: "1",
    month: "May 17",
    value: 1200,
    series: "Mock Data3",
  },
  {
    key: "3",
    month: "Jul 17",
    value: 2400,
    series: "Mock Data2",
  },
  {
    key: "2",
    month: "Jun 17",
    value: 900,
    series: "Mock Data3",
  },
];

function VCCLineChartComponent(props: VCCLineChartProps): JSX.Element {
  const { formatMessage } = useIntl();

  const processedData = processData({ values: data });

  const {
    viewTakeaways,
    colors,
    toolTipAccessors = [],
    toolTipTitle = [],
    width = 1075,
    height = 600,
  } = props;

  return (
    <VCCLineChart
      colors={colors}
      data={processedData}
      legend={{
        visible: false,
        labels: ["Montly Values"],
      }}
      mainTitle="Line Chart"
      padding={{
        top: 0,
        bottom: 50,
        right: 100,
        left: 80,
      }}
      seriesLabel={{
        visible: true,
        placement: "right",
      }}
      showTooltip={toolTipAccessors.length > 0}
      tooltipLabel={{
        labelAccessor: toolTipAccessors,
        labelTitle: toolTipTitle,
        format: [],
      }}
      subTitle="A prototype for setting up a Spectrum-ish Visa Line Charts"
      uniqueID="my-line-chart"
      xAxis={{
        visible: true,
        label: formatMessage(messages.month),
        format: "%b %y",
      }}
      yAxis={{
        visible: true,
        label: formatMessage(messages.impressions),
        tickInterval: 3,
      }}
      ordinalAccessor={"month"}
      dotRadius={6}
      accessibility={
        !viewTakeaways
          ? {
              executiveSummary:
                "The period from June and July showed the most notable monotonic increase in the number of impressions. It was 590 at the very beginning and increased by 1810 to 2400 in the end. The most notable decline in impressions occurred between May and June 2017 At the outset, it was 1550. At the close of this period, the impressions value had decreased by x% to 590.",
              contextExplanation:
                "This chart was created to represent the number of impressions over a period of 4 months. Each of the three lines on the chart representes a different group of impressions that have been recorded. This chart is composed of mock data for the purposes of experimentation.",
              statisticalNotes:
                "The total number of impressions was 13,801 over the months of April, May, June and July.  Throughout this period, the highest number of impressions was 2400, reached in June of 2017.",
              purpose:
                "This chart is a simple example to try and get VCC running with react-spectrum.",
              longDescription:
                "This graph is composed of three series of data. The first serires consists of three points, and shows a consistently upward trend. The second series is made up of four points, and trends upwards between the first points, downwards between the second and third point, then sharply upward at the final point. The third serires is made up of four points, and has one upward trend between the first and second point. The series then trends slightly downward, remaining consistently sloped between the third and fourth point.",
              structureNotes:
                "There are no special visual features here. Just three lines are displayed. The most notable decline in impressions occurred between May and June 2017 At the outset, it was 1550. At the close of this period, the impressions value had decreased by x% to 590.",
              elementsAreInterface: true,
              hideTextures: true,
              hideDataTableButton: true,
              disableValidation: true,
              includeDataKeyNames: true,
              elementDescriptionAccessor: "note",
            }
          : {
              executiveSummary:
                "The period from June and July showed the most notable monotonic increase in the number of impressions. It was 590 at the very beginning and increased by 1810 to 2400 in the end. The most notable decline in impressions occurred between May and June 2017 At the outset, it was 1550. At the close of this period, the impressions value had decreased by x% to 590.",
              elementsAreInterface: true,
              hideTextures: true,
              hideDataTableButton: true,
              disableValidation: true,
              includeDataKeyNames: true,
              elementDescriptionAccessor: "note",
            }
      }
      width={width}
      height={height}
    />
  );
}

/**
 * A prototype for Spectrum-ifying Visa Chart Components
 */
function VCCLineChartProto(props: VCCLineChartProps): JSX.Element {
  const { locale, ...otherProps } = props;

  return (
    <IntlProvider locale={locale} messages={localizedMessages}>
      <VCCLineChartComponent {...otherProps} />
    </IntlProvider>
  );
}

export default VCCLineChartProto;
