import "../index.css";
import * as localizedMessages from "../__localization__";
import {
  ActionButton,
  Button,
  ButtonGroup,
  Content,
  Dialog,
  DialogTrigger,
  Divider,
  Flex,
  Heading,
} from "@adobe/react-spectrum";
import {
  Cell,
  Counter,
  FiltersOptions,
  Inventory,
  PageHeader,
  Row,
  Search,
  Table,
  Title,
  ViewHeader,
} from "@quarry/inventory";
import React, { useState } from "react";
import { defineMessages, useIntl } from "react-intl";
import { DataItem } from "../types";
import { IntlProvider } from "@quarry/intl";
import ShowMenu from "@spectrum-icons/workflow/ShowMenu";
import { ToggleButton } from "@adobe/react-spectrum";
import VCCLineChartProto from "./VCCLineChart";
import { action } from "@storybook/addon-actions";

const messages = defineMessages({
  month: {
    id: "line-chart.month",
    defaultMessage: "Month",
  },
  impressions: {
    id: "line-chart.impressions",
    defaultMessage: "Impressions",
  },
  toggle: {
    id: "line-chart.toggle-aria-label",
    defaultMessage: "Toggle menu",
  },
});

const data: DataItem[] = [
  {
    key: "0",
    month: "Apr 17",
    value: 1407,
    series: "Mock Data",
  },
  {
    key: "2",
    month: "Jun 17",
    value: 2207,
    series: "Mock Data",
  },
  {
    key: "0",
    month: "Apr 17",
    value: 140,
    series: "Mock Data2",
  },
  {
    key: "1",
    month: "May 17",
    value: 1550,
    series: "Mock Data2",
  },
  {
    key: "1",
    month: "May 17",
    value: 1807,
    series: "Mock Data",
  },
  {
    key: "3",
    month: "Jul 17",
    value: 1000,
    series: "Mock Data3",
  },
  {
    key: "2",
    month: "Jun 17",
    value: 590,
    series: "Mock Data2",
  },
  {
    key: "0",
    month: "Apr 17",
    value: 600,
    series: "Mock Data3",
  },
  {
    key: "1",
    month: "May 17",
    value: 1200,
    series: "Mock Data3",
  },
  {
    key: "3",
    month: "Jul 17",
    value: 2400,
    series: "Mock Data2",
  },
  {
    key: "2",
    month: "Jun 17",
    value: 900,
    series: "Mock Data3",
  },
];

interface LineChartProps {
  /** Locale used to internationalize the component. */
  locale?: string;
  colors?: string[];
  toolTipAccessors: string[];
  toolTipTitle: string[];
  width?: number;
  height?: number;
  viewTakeaways?: boolean;
}

function LineChartComponent(props: LineChartProps): JSX.Element {
  const { formatMessage } = useIntl();

  const [items, setItems] = useState<DataItem[]>(data);
  const [isSelected, setSelected] = useState(true);

  const handleFilterChange = ({ query }: FiltersOptions) => {
    setItems(
      data.filter(
        (item) =>
          !query ||
          (item.month && item.month.toLowerCase().includes(query.toLowerCase()))
      )
    );

    action("onFilterChange")({ query });
  };

  const { viewTakeaways, ...otherProps } = props;

  return (
    <div>
      <div id="line-chart">
        <VCCLineChartProto {...props} />
      </div>
      <ToggleButton
        aria-label={formatMessage(messages.toggle)}
        isSelected={isSelected}
        onChange={setSelected}
      >
        <ShowMenu />
      </ToggleButton>
      {viewTakeaways && (
        <DialogTrigger type="fullscreen">
          <ActionButton>View Takeaways</ActionButton>
          {(close) => (
            <Dialog>
              <Heading>Line Chart Insights</Heading>
              <Divider />
              <Content>
                <Flex alignItems="start" gap="size-800">
                  <div className="dialog-graph">
                    <VCCLineChartProto
                      {...{ props, ...otherProps, width: 1075, height: 600 }}
                    />
                  </div>
                  <div className="instory-text">
                    <ul>
                      <li className="bullet-point">
                        The total number of impressions was 13,801 over the
                        months of April, May, June, and July. Throughout this
                        period, the highest number of impressions was 2,400,
                        reached in June of 2017.
                      </li>
                      <li className="bullet-point">
                        The most notable decline in impressions occurred between
                        May and June 2017. At the outset, it was 1550. At the
                        close of this period, the impressions value had
                        decreased by x% to 590.
                      </li>
                      <li className="bullet-point">
                        The period from June and July showed the most notable
                        monotonic increase in the number of impressions. It was
                        590 at the very beginning and increased by 1810 to 2400
                        in the end
                      </li>
                    </ul>
                  </div>
                </Flex>
              </Content>
              <ButtonGroup>
                <Button variant="secondary" onPress={close}>
                  Close
                </Button>
              </ButtonGroup>
            </Dialog>
          )}
        </DialogTrigger>
      )}
      <div>
        {isSelected && (
          <Inventory items={items} onFilterChange={handleFilterChange}>
            <PageHeader>
              <Title>Header</Title>
            </PageHeader>
            <ViewHeader>
              <Search />
              <Counter />
            </ViewHeader>
            <Table
              columns={[
                {
                  key: "month",
                  label: "Month",
                },
                {
                  key: "value",
                  label: "Impressions",
                },
                {
                  key: "series",
                  label: "Series",
                },
              ]}
              selectionMode="single"
            >
              {(data: any) => {
                return (
                  <Row key={data.value}>
                    {(column) => {
                      return <Cell>{data[column]}</Cell>;
                    }}
                  </Row>
                );
              }}
            </Table>
          </Inventory>
        )}
      </div>
    </div>
  );
}

/**
 * A prototype for Spectrum-ifying Visa Chart Components
 */
function LineChart(props: LineChartProps): JSX.Element {
  const { locale, ...otherProps } = props;

  return (
    <IntlProvider locale={locale} messages={localizedMessages}>
      <LineChartComponent {...otherProps} />
    </IntlProvider>
  );
}

export default LineChart;
