import { DataItem } from './types';
import groupBy from 'lodash/groupBy';
import sortBy from 'lodash/sortBy';

interface DataProps {
  values: DataItem[];
}

export function processData(props: DataProps): DataItem[] {
  const groups = groupBy(sortBy(props.values, ['series', 'key']), 'series');

  Object.keys(groups).forEach(key => {
    difference(groups[key]);
    minMaxValue(groups[key]);
  });
  return sortBy(props.values, ['series', 'key']);
}

function difference(props: DataItem[]) {
  let prevValue: DataItem;
  Object.values(props).forEach(entry => {
    if (entry.key == '0') {
      entry.differenceFromLast = 0;
      prevValue = entry;
    } else {
      entry.differenceFromLast = entry.value - prevValue.value;
      prevValue.differenceFromNext = entry.value - prevValue.value;
      getVerbalPreviousDifference(entry, entry.value - prevValue.value);
    }
    getVerbalNextDifference(prevValue, prevValue.value - entry.value);
    prevValue = entry;
  });
}

function getVerbalPreviousDifference(entry: DataItem, difference: number) {
  const absoluteValue = Math.abs(difference);
  const isNegative = difference < 0;

  entry.note = '';

  if (absoluteValue < 50) {
    entry.note += `this point is a little ${
      isNegative ? 'lower' : 'higher'
    } than the previous point. `;
  } else if (absoluteValue < 200) {
    entry.note += `this point is ${isNegative ? 'lower' : 'higher'} than the previous point. `;
  } else {
    entry.note += `this point is much ${isNegative ? 'lower' : 'higher'} than the previous point. `;
  }
}

function getVerbalNextDifference(entry: DataItem, difference: number) {
  const absoluteValue = Math.abs(difference);
  const isNegative = difference < 0;

  if (absoluteValue < 50) {
    entry.note += `this point is a little ${isNegative ? 'lower' : 'higher'} than the next point. `;
  } else if (absoluteValue < 200) {
    entry.note += `this point is ${isNegative ? 'lower' : 'higher'} than the next point. `;
  } else {
    entry.note += `this point is much ${isNegative ? 'lower' : 'higher'} than the next point. `;
  }
}

function minMaxValue(props: DataItem[]) {
  const values = Object.values(props).map(entry => {
    return entry.value;
  });

  const min = Math.min(...values);
  const max = Math.max(...values);

  Object.values(props).forEach(entry => {
    if (entry.value == min) {
      entry.minMax = 'minimum';
      entry.note += 'This is the minimum data point in the series.';
    } else if (entry.value == max) {
      entry.minMax = 'maximum';
      entry.note += 'This is the maximum data point in the series.';
    } else {
      entry.minMax = '';
    }
  });
}

export default processData;
